<template>
	<!-- 经营规模分析 -->
	<box-item title="经营规模分析">
		<div class="module-wrap">
			<p class="panel-sub-title">菜田面积合计</p>
			<p class="main-data">
				<i class="value-data"> {{ totalArea }} </i>万亩
			</p>
			<!-- 面积区间 -->
			<div class="interval-container">
				<ul class="interval-wrap">
					<li class="axis-item" v-for="(item, index) in scaleInterval" :key="index">
						<div class="axis-data">
							<p class="axis-name">{{ item.name }}</p>
							<p class="axis-value">
								<i class="b-v"> {{ item.value }} </i>家
							</p>
						</div>
					</li>
				</ul>
			</div>
		</div>
	</box-item>
</template>

<script>
import BoxItem from '@/components/common/BoxItem';
export default {
	name: 'BusinessScaleAnalysis',
	props: {
		totalArea: {
			type: Number,
			default: 0
		}
	},
	data() {
		return {
			// 规模间隙数据
			scaleInterval: [
				{
					name: '≥500亩',
					value: 69
				},
				{
					name: '100-500亩',
					value: 646
				},
				{
					name: '50-100亩',
					value: 551
				},
				{
					name: '10-50亩',
					value: 3082
				},
				{
					name: '<10亩',
					value: 14699
				}
			]
		};
	},
	created() {},
	mounted() {},
	methods: {},
	components: {
		BoxItem
	}
};
</script>

<style scoped lang="less">
.module-wrap {
	width: 100%;
	height: 100%;
	display: flex;
	justify-content: center;
	flex-direction: column;
}
.panel-sub-title {
	text-align: center;
	font-size: 18px;
	color: #6dc1cb;
	height: 28px;
	line-height: 28px;
	/*margin-top: 10px;*/
}
.main-data {
	font-size: 16px;
	color: #feeb48;
	text-align: center;
	height: 42px;
	margin-top: 5px;
	.value-data {
		font-size: 32px;
		font-weight: bold;
		color: #feeb48;
		font-style: normal;
		margin-right: -5px;
	}
}
.interval-container {
	display: flex;
	justify-content: center;
	margin: 15px auto 0;
	box-sizing: border-box;
	width: 100%;
	max-width: calc(420px * 1.5);
	.interval-wrap {
		display: flex;
		width: 88%;
		.axis-item {
			min-width: 69px;
			width: calc(100% / 4);
			position: relative;
			padding: 26px 0 0;
			/* 竖线 */
			&:before {
				content: '';
				height: 26px;
				width: 1px;
				background-color: #156d86;
				position: absolute;
				top: 0;
				left: 50%;
				transform: translateX(-50%);
			}
			/* 横线 */
			&:after {
				content: '';
				height: 1px;
				width: 100%;
				background-color: #156d86;
				position: absolute;
				top: 0;
				left: 50%;
				transform: translateX(-50%);
			}

			.axis-data {
				text-align: center;
				white-space: nowrap;
				padding: 0 10px;
			}
			.axis-name {
				font-size: 12px;
				color: #9dd1d7;
				height: 22px;
				line-height: 22px;
			}
			.axis-value {
				font-size: 14px;
				color: #feeb48;
				height: 25px;
				.b-v {
					font-size: 18px;
					margin-right: -4px;
				}
			}
			&:last-child {
				&:after {
					width: 50%;
					left: -1px;
					transform: translateX(0);
				}
			}
			&:first-child {
				&:after {
					width: 50%;
					left: auto;
					right: -1px;
					transform: translateX(0);
				}
			}
		}
	}
}
/* 32:9超宽屏幕适配 */
@media screen and (min-width: 3000px) {
	.panel-sub-title {
		font-size: 20px;
	}
	.main-data {
		font-size: 16px;
	}
	.interval-container {
		margin: 8px auto 0;
	}
	.interval-container .interval-wrap .axis-item .axis-name {
		font-size: 16px;
		height: 26px;
		line-height: 26px;
	}
	.interval-container .interval-wrap .axis-item .axis-value {
		font-size: 20px;
		height: 30px;
	}
	.interval-container .interval-wrap .axis-item .axis-value .b-v {
		font-size: 22px;
	}
}
</style>



